.container {
  max-width: 1200px;
  margin: 1rem auto;
}
.products h2 {
  text-transform: capitalize;
}

.search {
  margin: 1.5rem 0;
}
.search input {
  padding: 0.5rem;
  border: none;
  outline: 1px solid gainsboro;
  width: 400px;
  max-width: 400px;
  font-size: 1.1rem;
  border-radius: 6px;
}

.table {
  display: grid;
  grid-template-columns: 2fr 3fr 1fr 1fr 1fr 2fr;
  color: #45474b;
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
.table > div {
  /* border: 1px solid gainsboro; */
  padding: 1rem 0.5rem;
  text-transform: capitalize;
}
.even {
  background-color: #adc4ce67;
}
.odd {
  background-color: #fff;
}

.btnUpdate {
  padding: 0.5rem 1rem;
  margin-right: 0.5rem;
  background-color: #539165;
  border: none;
  color: #fff;
  text-transform: capitalize;
  cursor: pointer;
  text-decoration: none;
}
.btnDelete {
  padding: 0.5rem 1rem;
  background-color: #ce5a67;
  border: none;
  color: #fff;
  text-transform: capitalize;
  cursor: pointer;
  text-decoration: none;
}
