.form {
  border: 1px solid gainsboro;
  max-width: 1200px;
  margin: 1rem auto;
  padding: 1rem 1rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 2rem;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  border-radius: 5px;
}
.input {
  /* margin-bottom: 1.6rem; */
}
.input label {
  display: block;
  margin-bottom: 0.6rem;
  font-size: 1.2rem;
  color: rgb(114, 113, 113);
  font-weight: bold;
  text-transform: capitalize;
}
.input input {
  width: 100%;
  border: none;
  outline: 1px solid gainsboro;
  font-size: 1.2rem;
  padding: 0.6rem;
  border-radius: 5px;
}
.input select {
  width: 100%;
  border: none;
  outline: 1px solid gainsboro;
  font-size: 1.2rem;
  padding: 0.6rem;
  border-radius: 5px;
}
.input textarea {
  width: 100%;
  border: none;
  outline: 1px solid gainsboro;
  font-size: 1.2rem;
  padding: 0.6rem;
  border-radius: 5px;
}

.input button {
  padding: 0.5rem 1rem;
  border: none;
  outline: 1px solid gainsboro;
  background-color: cadetblue;
  color: #fff;
  font-size: 1.2rem;
  border-radius: 5px;
  text-transform: capitalize;
}
.input button:hover {
  background-color: #fff;
  color: cadetblue;
  border: 2px solid gainsboro;
}

.err ul {
  padding: 1rem;
}
.err ul li {
  color: red;
  text-transform: capitalize;
  text-align: center;
}
