.container {
  background-color: #fff;
  margin-top: 2rem;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  padding: 2rem;
}
.grid {
  margin-top: 2rem;
  display: grid;
  grid-template-columns: 2fr 2fr 1fr 2fr 1fr 1fr 1fr 1fr;
}
.head {
  border: 1px solid gainsboro;
  padding: 1rem;
  text-transform: capitalize;
  font-weight: bold;
  background-color: #9bb8cd;
}
.body {
  border: 1px solid gainsboro;
  padding: 1rem;
  display: flex;
  align-items: center;
  font-size: 1.1rem;
}
.body button {
  padding: 0.5rem 1rem;
  background-color: #86a789;
  color: #fff;
  cursor: pointer;
  font-size: 1.1rem;
  border-radius: 5px;
  border: none;
}
