.container {
  position: relative;
  height: 100vh;
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url("https://images.pexels.com/photos/434337/pexels-photo-434337.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1");
  background-repeat: no-repeat;
  background-position: left bottom;
  background-size: cover;
  z-index: 1;
}
.form {
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 1px solid gainsboro;
  padding: 1.2rem;
  width: 350px;
  max-width: 500px;
  border-radius: 10px;
  background-color: #fff;
  z-index: 1000;
}

.form h2 {
  text-align: center;
  font-size: 2rem;
  margin-bottom: 2rem;
  color: cadetblue;
}

.input {
  margin-bottom: 2rem;
}

.input label {
  display: block;
  font-size: 1.2rem;
  margin-bottom: 1rem;
  text-transform: capitalize;
  font-weight: bold;
}
.input input {
  padding: 1rem;
  border: none;
  outline: 1px solid gainsboro;
  font-size: 1.2rem;
  width: 100%;
  border-radius: 10px;
}

.btn {
  display: flex;
  justify-content: center;
}

.form button {
  display: inline-block;
  text-align: center;
  padding: 0.5rem 1rem;
  font-size: 1.2rem;
  background-color: transparent;
  border: none;
  outline: 2px solid cadetblue;
  color: cadetblue;
  text-transform: capitalize;
  border-radius: 5px;
}

.form button:hover {
  background-color: cadetblue;
  color: #fff;
  transform: scale(1.2);
  outline: none;
}

.err {
  margin-bottom: 1rem;
}
.err ul {
  padding: 0 1rem;
}
.err ul li {
  color: red;
  text-transform: capitalize;
}
