.nav {
  height: 70px;
  outline: 1px solid gainsboro;
  padding: 0 2rem;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.nav ul {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
  gap: 3rem;
  height: 100%;
}
.nav ul li {
  font-size: 1.2rem;
  text-transform: capitalize;
}

.nav ul li:first-of-type {
  margin-right: auto;
  font-size: 2rem;
  font-weight: bold;
}

.nav a {
  text-decoration: none;
}
a:hover {
  color: orange;
}
.active {
  color: orange;
}
.inactive {
  color: #000;
}
.btn {
  display: inline-block;
  padding: 0.5rem 1rem;
  border-radius: 10px;
  background-color: orange;
  color: #fff;
  border: none;
  cursor: pointer;
  font-size: 1.2rem;
}
.btn:hover {
  color: orange;
  background-color: #d5d4d4;
}
