.pagination {
  margin-top: 2rem;
  display: flex;
  gap: 1rem;
  justify-content: flex-end;
  margin-right: 4rem;
}

.pagination a {
  display: inline-block;
  padding: 0.5rem;
  text-transform: capitalize;
}
.current {
  border-radius: 50%;
  color: #fff;
  padding: 0.5rem 1rem !important;
  background-color: #4682a9;
  text-decoration: none;
}
